.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  font-family: Arial, Helvetica, san-serif;
}

body{
  margin: 0;
  padding: 0;
}
.addTask{
  height: 30vh;
  background-color: dodgerblue;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.list{
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}
.task{
  width: 500px;
  height: 50px;
  display: flex;
  margin: 15px;
  align-items: center;
  justify-content: center;
}
.task .button2{
  width: 25px;
  height: 20px;
  margin-left: 20px;
  text-align: center;
}
.task .button1{
  width: 100px;
  height: 20px;
  margin-left: 20px;
  text-align: center;
}